import {
  Card,
  Breadcrumb,
  Form,
  Button,
  Radio,
  Input,
  Upload,
  Space,
  Select,
  message
} from 'antd'
import { PlusOutlined } from '@ant-design/icons'
import { useState, useEffect } from 'react';
import ReactQuill from 'react-quill';
import { Link, useSearchParams, useNavigate } from 'react-router-dom'
import { fetchCreeateArticlesApi, getArticleAPI } from '@/apis/article'
import { useChannel } from '@/hooks/useChannel'
import 'react-quill/dist/quill.snow.css';
import './index.scss'

const { Option } = Select

const Publish = () => {
  const navigator = useNavigate
  const [form] = Form.useForm();
  const [ fileList, setFileList ] = useState([])
  const [ coverType, setCoverType ] = useState(0)
  const { channels } = useChannel()
  const [ articleForm, setArticleForm ] = useState({})
  const [ searchParams ] = useSearchParams()
  const id = searchParams.get('id');

  function handleFinish(values) {
    console.log(values);
    const { type } = values
    if (fileList.length !== type) {
      message.error('')
      return
    }

    if (id) {
      updateArticle(values)
    } else {
      createrArticle(values)
    }
  }

  function updateArticle(values) {
    console.log('values--------', values);
    const { title, channel_id, content, type } = values
    fetchCreeateArticlesApi({
      id,
      title,
      content,
      channel_id,
      pub_date: articleForm.pub_date,
      cover: {
        type: type,
        images: fileList.map(item => item.response ? item.response.data.url : item.url)
      }
    }).then(res => {
      message.success('')
      console.log(res)
      setFileList([]) 
      setCoverType(0)
      form.resetFields()
      navigator('/layout/article')
    }).catch(err => {
      console.log(err);
      message.error('')
    })
  }

  function createrArticle(values) {
    const { title, channel_id, content, type } = values
    fetchCreeateArticlesApi({
      title,
      content,
      channel_id,
      cover: {
        type: type,
        images: fileList.map(item => item.response ? item.response.data.url : item.url)
      }
    }).then(res => {
      message.success('')
      console.log(res)
      setFileList([]) 
      setCoverType(0)
      form.resetFields()
    }).catch(err => {
      console.log(err);
      message.error('')
    })
  }

  function handleUploadChange({ fileList: newFileList }) {
    console.log('handleUploadChange', newFileList);
    setFileList(newFileList)
  }

  function handleRadioChange({ target }) {
    console.log('handleRadioChange', target);
    const { value } = target
    setFileList([])
    setCoverType(value)
  }

  useEffect(() => {
    if (id) {
      getArticleAPI(id).then(res => {
        form.setFieldsValue(res.data.data)
        form.setFieldValue('type', res.data.data.cover.type)
        setArticleForm(res.data.data)
        setCoverType(res.data.data.cover.type)
        setFileList(res.data.data.cover.images.map(item => ({ url: item })))
      })
    }
  }, [id, form])

  return (
    <div className="publish">
      <Card
        title={
          <Breadcrumb items={[
            { title: <Link to={'/'}></Link> },
            { title: '' },
          ]}
          />
        }
      >
        <Form
          labelCol={{ span: 4 }}
          wrapperCol={{ span: 16 }}
          initialValues={{ type: 0 }}
          form={form}
          onFinish={handleFinish}
        >
          <Form.Item
            label=""
            name="title"
            rules={[{ required: true, message: '' }]}
          >
            <Input placeholder="" style={{ width: 400 }} />
          </Form.Item>
          <Form.Item
            label=""
            name="channel_id"
            rules={[{ required: true, message: '' }]}
          >
            <Select placeholder="" style={{ width: 400 }}>
              {
                channels.map(item => <Option key={item.id} value={item.id}>{ item.name }</Option>)
              }
            </Select>
          </Form.Item>
          <Form.Item label="">
            <Form.Item name="type">
              <Radio.Group onChange={ handleRadioChange }>
                <Radio value={1}></Radio>
                <Radio value={3}></Radio>
                <Radio value={0}></Radio>
              </Radio.Group>
            </Form.Item>
            {
              coverType > 0 && (
                <Upload
                  listType="picture-card"
                  fileList={ fileList }
                  showUploadList
                  name="image"
                  maxCount={ coverType }
                  action="http://geek.itheima.net/v1_0/upload"
                  onChange={ handleUploadChange }
                >
                  <div style={{ marginTop: 8 }}>
                    <PlusOutlined />
                  </div>
                </Upload>
              )
            }
          </Form.Item>
          <Form.Item
            label=""
            name="content"
            rules={[{ required: true, message: '' }]}
          >
            <ReactQuill className="publish-quill" placeholder="" theme="snow" />
          </Form.Item>

          <Form.Item wrapperCol={{ offset: 4 }}>
            <Space>
              <Button size="large" type="primary" htmlType="submit">
                
              </Button>
            </Space>
          </Form.Item>
        </Form>
      </Card>
    </div>
  )
}

export default Publish